import React, { useMemo } from 'react';
import { Block } from '@ali/4ever-cangjie';
import { MultibarLine } from '../styled';
import { calcMultibarPosition } from '../utils';

export default function Multibar(props: { node: Block }) {
  const { node } = props;
  const { left, symbolWidth } = useMemo(() => calcMultibarPosition(node), [node]);
  const paddingLeft = left + symbolWidth / 2;
  return <MultibarLine paddingLeft={paddingLeft} />;
}
